import React, { useState, useRef, useEffect } from "react";
import { toast } from "react-toastify";
import { directusLoaderLink } from "../../utils/constants";
import ConfirmationModal from "../Modal/confirmationModal";
import { addDays, format } from "date-fns";
import Select from "react-select";
import { useHydrated } from "remix-utils";

function ConferenceModal({
  date,
  locationId,
  roomDetail,
  showPlaceHolder,
  content,
  setRefresh,
  refresh,
  popupView,
  isAdmin,
}) {
  const isHydrated = useHydrated();
  const DateFormat = "yyyy-MM-dd";
  const [roomName, setRoomName] = React.useState(
    !!content?.room_id ? content?.room_id : ""
  );
  const [companyName, setCompanyName] = React.useState(
    !!content?.company_name ? content?.company_name : ""
  );
  const [modalIsOpen, setIsOpen] = useState(false);
  const [modalLoader, setModalLoader] = useState(false);
  const [isDivVisible, setDivVisible] = useState(false);
  const [preventDouble, setPreventDouble] = useState(false);
  const divRef = useRef(null);
  const handleClickOutside = (event) => {
      setDivVisible(false);
      setCompanyName(!!content?.company_name ? content?.company_name : "");
      setRoomName(!!content?.room_id ? content?.room_id : "");
  };
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (divRef.current && !divRef.current.contains(event.target)) {
        setDivVisible(false);
        setCompanyName(!!content?.company_name ? content?.company_name : "");
        setRoomName(!!content?.room_id ? content?.room_id : "");
      }
    };
    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);
  const toggleDiv = async () => {
    setDivVisible(!isDivVisible);
  };
  const createHandle = async (e) => {
    const directus = directusLoaderLink;
    setPreventDouble(true);
    if (companyName?.trim()?.length > 0) {
      if (!!roomName) {
        if (content?.id) {
          // update handle
          await directus
            .items("conference")
            .updateOne(content?.id, {
              date: format(date, DateFormat),
              company_name: companyName,
              room_id: roomName?.id,
              location_id: locationId,
            })
            .then((res) => {
              toast.success("Updated successfully");
              setDivVisible(false);
              location.reload();
              setRefresh(!refresh);
            })
            .catch((err) => {
              // navigate(".", { replace: true });
              console.log(err);
              toast.error(err);
            });
        } else {
          // create handle
          await directus
            .items("conference")
            .createOne({
              date: format(date, DateFormat),
              company_name: companyName,
              room_id: roomName?.id,
              location_id: locationId,
            })
            .then((res) => {
              toast.success(" Created successfully");
              setDivVisible(false);
              setRefresh(!refresh);
            })
            .catch((err) => {
              // navigate(".", { replace: true });
              console.log(err);
              toast.error(err);
            });
        }
        localStorage.setItem(
          "ConferenceWacthedWeek",
          new Date(date)?.getDay() == 0
            ? format(addDays(new Date(date), -2), "yyyy-MM-dd")
            : date
        );
      } else {
        toast.error("Rooms name is required");
      }
    } else {
      toast.error("Company name is required");
    }
    setPreventDouble(false);
  };

  const RepeatSelection = () => {
    const onChangeHandle = (e) => {
      setRoomName(e);
      return "";
    };
    return (
      <div>
        {isHydrated ? (
          <Select
            id={"rooms"}
            instanceId={"roomId"}
            classNamePrefix="cursor-pointer react-select"
            className="react-select-container font-normal text-sm w-full"
            maxMenuHeight={250}
            onChange={(e) => onChangeHandle(e)}
            isSearchable={false}
            components={{ IndicatorSeparator: () => null }}
            value={roomName}
            options={roomDetail}
            getOptionLabel={(option) => option.name}
            getOptionValue={(option) => option}
            styles={{
              control: (baseStyles) => ({
                ...baseStyles,
                border: "none",
                boxShadow: "none",
              }),
            }}
          />
        ) : null}
      </div>
    );
  };
  const deleteHandle = async () => {
    if (content?.id) {
      setModalLoader(true);
      await directusLoaderLink
        .items("conference")
        .deleteOne(content?.id)
        .then((res) => {
          toast.success("Data deleted successfully");
        })
        .catch((err) => {
          console.log(err);
        });
      setRefresh(!refresh);
      setModalLoader(false);
      location.reload();
    }
  };
  const cancelHandle = () => {
    setIsOpen(false);
  };
  React.useEffect(() => {
    if (companyName?.length > 100) {
      setCompanyName(companyName?.substring(0, 100));
    }
  }, [companyName]);
  return (
    <div className={`relative ${popupView ? "c-popup-child" : ""}`}>
      <div
        className={`tasksname font-futura p-2 text-left font-bold ${
          showPlaceHolder ? "no-data" : ""
        }`}
        onClick={toggleDiv}
      >
        {showPlaceHolder ? (
          <>
            <span className="flex">
              <i className="ri-add-fill color-primary mr-2"></i>
              Add company
            </span>
          </>
        ) : (
          <>{content?.company_name}</>
        )}
      </div>
      {isDivVisible && (
        <div className="div-block" ref={divRef}>
          <div
            className={`addcompany flex flex-col p-4 font-normal absolute min-w-[300px] min-h-[300px] bg-[#E7ECE6] border-1 z-10 gap-3 text-sm rounded-[7px]`}
          >
            <div className="flex justify-end">
              <button className="close-act" onClick={handleClickOutside}>
                <i className="ri-close-line text-primary"></i>
              </button>
            </div>
            <div className="col-span-6 mb-3">
              <label
                htmlFor="room-name"
                className="block text-sm font-medium text-gray-700 uppercase"
              >
                Company
              </label>
              <div className="relative conference-name">
                <textarea
                  type="text"
                  name="company-name"
                  id="company-name"
                  placeholder="Company Name"
                  required
                  // ref={roomRef}
                  value={companyName}
                  onChange={(e) => setCompanyName(e.target.value)}
                  autoComplete="given-name"
                  className="resize-none  min-h-[80px] mt-1 block w-full rounded-[3px] border border-gray-300 py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
                />
                <span className="text-[10px] absolute bottom-0 right-2">
                  {companyName?.length}/100
                </span>
              </div>
            </div>{" "}
            <div className="col-span-6 mb-3">
              <label
                htmlFor="room-name"
                className="block text-sm font-medium text-gray-700 uppercase"
              >
                ROOM
              </label>
              {RepeatSelection(false)}
            </div>
            <div
              className={`flex pt-6 ${
                content?.id && isAdmin ? "justify-between" : "justify-end"
              }`}
            >
              {content?.id && isAdmin && (
                <button
                  className="py-1 px-2 border text-white border-red-700 bg-red-700 font-medium rounded-[5px] uppercase min-w-[100px] text-[12px]"
                  onClick={() => setIsOpen(true)}
                  title="delete"
                >
                  <i className="ri-delete-bin-line"></i>
                </button>
              )}
              <button
                className="py-1 px-2 border text-white border-primary bg-primary font-medium rounded-[5px] uppercase min-w-[100px] text-[12px]"
                onClick={() => createHandle()}
                disabled={preventDouble}
                title="save"
              >
                {preventDouble ? "Saving..." : "Save"}
              </button>
            </div>
          </div>
        </div>
      )}
      {modalIsOpen && (
        <ConfirmationModal
          modalIsOpen={modalIsOpen}
          setIsOpen={setIsOpen}
          content="Are you sure to delete data?"
          callFunction={deleteHandle}
          cancelHandle={cancelHandle}
          Loader={modalLoader}
        />
      )}
    </div>
  );
}

export default ConferenceModal;
